.hero {
    width: 100%;
    border-radius: 25px;
    .container {
        height: 100%;
    }
    &__wrap {
        padding-bottom: 30px;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        padding-top: 90px;
    }
    &__title {
        margin: 0;
        padding: 0;
        font-size: 42px;
        font-weight: bold;
        color: #fff;
        max-width: 440px;
        text-align: center;
    }
}

.benefits {
    padding: 80px 0;
    &__title {
        margin: 0;
        max-width: 350px;
        font-weight: 700;
        font-size: 28px;
        line-height: 106.9%;
        color: #BB2649;
        span {
            color: #FF8000;
        }
    }
    &__block {
        width: 100%;
    }
    &__wrap {
        gap: 150px;
    }
    &__item {
        max-width: 150px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 7px;
    }
    &__name {
        text-align: center;
        padding: 0;
        margin: 0;
        font-weight: 700;
        font-size: 16px;
    }
    &__descr {
        text-align: center;
        font-size: 14px;
    }
}

.immediate {
    &__title {
            text-align: center;
    }
    .table {
        border-radius: 5px;
        margin-top: 30px;
        display: flex;
        gap: 0;
        width: 100%;
        &__col {
            width: 38%;
        }
        &__col:first-child {
            width: 33%;
        }
        &__col:last-child {
            width: 30%;
        }
        &__price {
            padding: 0;
            margin: 0;
            font-weight: bold;
            font-size: 22px;
            color: #BB2649;
            span {
                opacity: .4;
            }
        }
    }
    .col {
        &__header {
            padding: 15px;
            background: #FCF4F4;
        }
        &__name {
            padding: 0;
            margin: 0;
            font-size: 18px;
            font-weight: 500;
        }
        &__content {
            height: 60px;
            box-sizing: border-box;
            gap: 15px;
            display: flex;
            align-items: center;
            padding: 15px;
            font-size: 14px;
        }
        &__content.cities {
            padding: 15px 0;
        }
        &__date {
            padding: 6px 11px;
            background: #BB2649;
            border-radius: 8px;
            color: #fff;
        }
    }
    .table__row {
        display: flex;
        align-items: center;
        .content__block {
            background: rgba(187, 38, 73, 0.09);
            border-radius: 5px;
            padding: 5px 15px 5px 25px;
            display: flex;
            align-items: center;
            gap: 7px;
            background: url('../img/table-element.png') center no-repeat;
            background-size: 100%, 100%;
        }
    }
    .price__col {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .price__col {
        button {
            color: #BB2649;
            padding: 0;
            margin: 0;
            border: none;
            outline: none;
            background: transparent;
            cursor: pointer;
            font-size: 15px;
            transition: all .2s ease-in-out;
            border-radius: 5px;
            padding: 0 20px;
            height: 60px;
            margin-right: -20px;
        }
        button:hover {
            background: rgba(187, 38, 73, 1);
            color: #fff;
        }
    }
    .table__col {
        .col__content:nth-child(2n + 1) {
            background: #FCF4F4;
        }
    }
    .table__col:nth-child(2) {
        .col__content:nth-child(2n + 1) {
            background: #fff;
        }
        .table__row {
            background: transparent !important;
        }
        .table__row:nth-child(2n + 1) .col__content {
            background: #FCF4F4;
        }
    }
}

.offer {
    padding: 80px 0;
    &__all {
        white-space: nowrap;
        font-size: 14px;
        display: flex;
        gap: 5px;
        padding: 20px;
        background: #FFF9F9;
        border-radius: 5px;
    }
    &__header {
        margin-bottom: 50px;
    }
    &__grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
}

.item {
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    border-radius: 15px;
    &__content {
        background-color: rgba(255, 255, 255, 0.85);
        border-radius: 10px;
    }
    &__header {
        background: #fff;
        border-radius: 10px;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__cities {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 18px;
    }
    &__days {
        margin: 0;
        padding: 0;
        padding: 5px 12px;
        color: rgba(255, 128, 0, 1);
        background: rgba(255, 128, 0, 0.1);
        border-radius: 8px;
    }
    &__benefits {
        padding: 20px;
        display: grid;
        gap: 20px;
        grid-template-columns: 1fr 1fr;
    }
    &__benefit {
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 13px;
    }
    &__bottom {
        padding: 15px;
        display: flex;
        align-items: center;
    }
    &__program {
        padding: 15px;
        display: flex;
        align-items: center;
        gap: 10px;
        background: #FFF9F9;
        border-radius: 10px;
        font-size: 14px;
        max-width: 215px;
    }
    .price__block {
        display: flex;
        flex-direction: column;
        gap: 3px;
        margin-left: auto;
    }
    &__day {
        max-width: 32px;
        color: #BB2649;
        font-size: 8px;
        padding: 3px 5px;
        background: rgba(187, 38, 73, 0.06);
        border-radius: 5px;
        width: auto;
    }
    &__price {
        padding: 0;
        margin: 0;
        color: #BB2649;
        font-size: 11px;
        span {
            font-size: 20px;
        }
    }
    &__btn {
        font-size: 15px;
         margin-left: auto;
        padding: 15px 20px;
        max-width: 150px;
    }
}

.news-block {
    .container {
        position: relative;
    }
    &__header {
        display: flex;
        gap: 30px;
        align-items: center;
    }
    .arrow__block {
        position: absolute;
        right: 15px;
        top: 15px;
        display: flex;
        gap: 25px;
        align-items: center;
        img {
            cursor: pointer;
            opacity: 1;
            transition: opacity .2s ease-in-out;
        }
        img:hover {
            opacity: 0.7
        }
    }
    &__content {
        margin-top: 30px;
    }
}

.news-item {
    position: relative;
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    border-radius: 15px;
    &__img {
        max-height: 170px;
        width: 100%;
        border-radius: 15px;
    }
    &__wrap {
        padding: 12px;
    }
    &__title {
        font-size: 18px;
        margin: 0;
        padding: 0;
    }
    &__descr {
        margin: 0;
        padding: 0;
        padding-top: 10px;
        font-size: 12px;
    }
    &__read {
        margin-top: 20px;
        padding: 18px;
        display: flex;
        justify-content: center;
    }
    &__date {
        position: absolute;
        top: 7px;
        left: 7px;
        color: #fff;
        opacity: 0.7;
        background: rgba(0, 0, 0, 0.14);
        border-radius: 23px;
        padding: 7px 12px;
        font-size: 11px;
    }
}

.benefit-tour {
    background: url('../img/benefit-bg.jpg') center no-repeat;
    background-size: 100% 100%;
    margin-top: 80px;
    padding: 60px 0;
    &__wrap {
        position: relative;
    }
    .title {
        color: #fff;
    }
    &__block {
        max-width: 580px;
    }
    &__descr {
        color: #fff;
        padding: 0;
        margin: 0;
        padding-top: 10px;
        font-size: 15px;
    }
    .benefit-tour__btn {
        margin-top: 30px;
    }
    &__img {
        position: absolute;
        right: 0;
        top: -120px;
    }
}

@media (max-width: 992px) {
    .benefit-tour {
        margin-top: 60px;
        padding: 50px 0;
        &__block {
            max-width: 480px;
        }
        .benefit-tour__btn {
            margin-top: 25px;
        }
        &__img {
            top: -100px;
            width: 50%;
        }
    }
    .news-block__content {
        margin-top: 20px;
    }
    .news-block .arrow__block {
        display: none;
    }
    .offer__all {
        padding: 15px;
    }
    .offer__grid {
        gap: 20px;
    }
    .item__program {
        max-width: 160px;
    }
    .item__bottom {
        gap: 5px;
    }
    .item__btn {
        font-size: 14px;
    }
    .item__price span {
        font-size: 18px;
    }
    .col__name {
        font-size: 15px !important;
    }
    .table__price {
        font-size: 18px !important;
    }
    .col__content {
        gap: 12px !important;
        font-size: 13px !important;
        height: 52px !important;
    }
    .table {
        height: 260px;
        overflow-x: scroll;
        &__col {
            width: 450px !important;
        }
        &__col:first-child {
            width: 300px !important;
        }
        &__col:last-child {
            width: 275px !important;
        }
        .price__col {
            width: 275px !important;
        }
    }
    .benefits {
        padding: 70px 0;
    }
    .benefits__wrap {
        gap: 70px;
    }
    .hero__wrap {
        padding-top: 80px;
    }
    .hero__title {
        font-size: 36px;
    }
    .offer {
        padding: 60px 0;
    }
}

@media (max-width: 768px) {
    .benefit-tour {
        margin-top: 50px;
        &__block {
            max-width: 100%;
        }
        &__img {
            display: none;
        }
    }
    .offer__header {
        margin-bottom: 40px;
    }
    .offer__grid {
        grid-template-columns: 1fr;
    }
    .benefits__title {
        text-align: center;
    }
    .benefits__wrap {
        flex-direction: column;
        gap: 35px;
    }
}

@media (max-width: 576px) {
    .benefit-tour {
        padding: 40px 0;
    }
    .benefits {
        padding: 60px 0;
    }
    .benefits__title {
        font-size: 24px;;
    }
    .hero__title {
        font-size: 32px;
    }
    .benefits__block {
        flex-direction: column;
        gap: 30px;
    }
    .item__cities {
        gap: 5px;
    }
    .item__days {
        font-size: 12px;
        padding: 5px 7px;
    }
}

@media (max-width: 480px) {
    .item__cities {
        font-size: 16px;
    }
    .item__program {
        padding: 12px;
        max-width: 125px;
    }
    .item__price span {
        font-size: 16px;
    }
    .item__bottom {
        padding: 10px;
    }
    .item__btn {
        font-size: 13px;
        padding: 12px 15px;
        max-width: 140px;
    }
}